.about {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 10%;
    background-color: #f9f9f9;
  }
  
  .about-photo {
    flex: 1;
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
  }
  
  .photo {
    width: 250px;
    height: 250px;
    border-radius: 50%;
    object-fit: cover;
    border: 4px solid black;
  }
  
  .about-info {
    flex: 2;
    max-width: 600px;
    text-align: center;
  }
  
  .about-info h2 {
    font-size: 2em;
    color: #333;
    margin-bottom: 10px;
  }
  
  .about-info h3 {
    font-size: 1.2em;
    color: #666;
    margin-bottom: 20px;
    font-weight: 300;
  }
  
  .about-info p {
    color: #555;
    line-height: 1.6;
    margin-bottom: 15px;
  }
  
  .about-interests {
    margin-top: 20px;
  }
  
  .about-interests h4 {
    font-size: 1.3em;
    color: #333;
    margin-bottom: 10px;
  }
  
  .about-interests ul {
    list-style: none;
    padding: 0;
  }
  
  .about-interests li {
    font-size: 1em;
    color: #666;
    margin-bottom: 8px;
  }
  
  @media (min-width: 768px) {
    .about {
      flex-direction: row;
      text-align: left;
    }
  
    .about-photo {
      margin-right: 20px;
      margin-bottom: 0;
    }
  
    .about-info {
      text-align: left;
    }
  }
  