/* Projects.css */

.projects {
    padding: 40px 10%;
    text-align: center;
    background-color: #ffffff;
    color: #000000;
  }
  
  .projects h2 {
    font-size: 2em;
    color: #000;
    margin-bottom: 20px;
    letter-spacing: 0.05em;
  }
  
  .projects-grid {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .project-card {
    background: #ffffff;
    border: 1px solid #000;
    padding: 20px;
    width: calc(50% - 10px);
    border-radius: 8px;
    text-align: left;
    box-shadow: 4px 4px 0px #000;
    transition: transform 0.3s ease, box-shadow 0.3s ease, background-color 0.3s ease;
    cursor: pointer;
  }
  
  .project-card:hover {
    transform: translateY(-5px) scale(1.02);
    box-shadow: 8px 8px 0px #000;
    background-color: #f5f5f5;
  }
  
  .project-card:active {
    transform: scale(0.98);
    box-shadow: 4px 4px 0px #000;
  }
  
  .project-image {
    width: 100%;
    height: auto;
    border-radius: 4px;
    margin-bottom: 10px;
    filter: grayscale(100%);
    transition: filter 0.3s ease;
  }
  
  .project-card:hover .project-image {
    filter: grayscale(0%);
  }
  
  .project-card h3 {
    margin-top: 0;
    color: #000;
    font-size: 1.25em;
  }
  
  .project-card p {
    color: #333;
    font-size: 0.95em;
  }
  
  .project-card a {
    display: inline-block;
    margin-top: 10px;
    color: #000;
    text-decoration: none;
    font-weight: bold;
    border-bottom: 1px solid transparent;
    transition: border-color 0.3s ease;
  }
  
  .project-card a:hover {
    border-color: #000;
  }
  
  .project-card a:active {
    color: #555;
    border-color: #555;
  }
  