.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 10%;
    background-color: #000;
    color: #fff;
    position: relative;
  }
  
  .header h1 {
    font-size: 1.8em;
    font-weight: bold;
    color: #fff;
  }
  
  .header nav a {
    margin: 0 10px;
    text-decoration: none;
    color: #fff;
    transition: color 0.3s ease;
  }
  
  .header nav a:hover {
    color: #007acc;
  }
  
  .menu-toggle {
    display: none;
    font-size: 1.8em;
    color: #fff;
    background: none;
    border: none;
    cursor: pointer;
  }
  
  /* Mobile Styling */
  @media (max-width: 768px) {
    .menu-toggle {
      display: block;
    }
  
    .nav-links {
      display: flex;
      flex-direction: column;
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      background-color: #000;
      text-align: center;
      max-height: 0;
      overflow: hidden;
      transition: max-height 0.3s ease;
    }
  
    .nav-links.open {
      max-height: 200px; /* Adjust based on the number of links */
    }
  
    .nav-links a {
      padding: 15px;
      margin: 0;
      border-top: 1px solid #333;
    }
  
    .nav-links a:hover {
      color: #007acc;
      background-color: #111;
    }
  }
  
  /* Desktop Styling */
  @media (min-width: 769px) {
    .nav-links {
      display: flex;
    }
  }
  
  .avatar {
    width: 60px; /* Default size for desktop */
    height: 60px;
    border-radius: 50%;
    object-fit: cover;
  }
  
  @media (max-width: 1024px) { /* Tablet */
    .avatar {
      width: 35px;
      height: 35px;
    }
  }
  
  @media (max-width: 768px) { /* Mobile */
    .avatar {
      width: 30px;
      height: 30px;
    }
  }

  .header-title {
    display: flex;
    align-items: center; /* Vertically center-aligns the items */
  }
  
  .header-title h1 {
    font-size: 1.8em;
    font-weight: bold;
    color: #fff;
    margin-left: 30px; /* Adds space between the avatar and name */
  }